.zoomableImage {
  max-width: 80vw;
  margin: 0 auto;
}

.zoomable-image-container {
  max-width: 80vw;
  height: auto;
  margin: 0 auto;
  overflow: scroll;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3rem 1rem;
  /* padding: 2rem; */

}

.zoomable-image-container .zoomable-image {
  width: 100%;
  height: 100%;
  transition: transform 0.3s ease;


}

.zoom-controls {
  display: flex;
  justify-content: start;

}

.zoom-controls button {
  margin-right: 1rem;
  padding: 10px 20px;
  cursor: pointer;
  background-color: black;
  color: #fff;
  border: none;
  border-radius: 4px;
  outline: none;
}

.zoom-controls button:hover {
  background-color: black;
  opacity: 70%;
}


/* Medium Devices (Larger Tablets and Small Laptops) */
@media screen and (min-width: 993px) and (max-width: 1280px) {
  .zoomable-image-container {
    /* margin-bottom: var(--image-margin-m); */
    padding: 1.5rem 3rem;
  }
}